.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}
.header-bg{
    background-color: rgba(0,0,0,0.85);
    transition: 0.5s;
}

.nav-menu {
    display: flex;
    list-style: none;
}

.nav-menu li {
    padding: 0 1rem;
}

.nav-menu li a {
    font-size: 1.2rem;
    font-weight: 500;
    text-decoration: none;
    color: white;
}

.hamburger {
    display: none;
    cursor: pointer;
    z-index: 1000;
}

@media screen and (max-width: 1040px) {
    .nav-menu {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.9);
        position: absolute;
        top: 0;
        left: -100%; 
        z-index: 999;
        transition: 0.3s ease;
    }

    .nav-menu.active {
        left: 0; 
    }

    .nav-menu li {
        padding: 1rem 0;
    }

    .nav-menu li a {
        font-size: 2rem;
    }

    
    .hamburger {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        cursor: pointer;
    }

    .hamburger div {
        width: 30px;
        height: 3px;
        background-color: white;
    }
}
