.projectheading{
    text-align: center;
    padding: 4rem 0 2rem 0;

}
.projectcontainer{
    max-width: 1140px;
    margin:0 auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap:40px;
    padding-bottom: 5rem;
}
.projectcard{
    background: #1a1919;
    padding: 1.2rem 1rem;
}
.projectcard img{
    width: 100%;
}
.projecttitle{
    color:#fff;
    padding: 1rem 0;
}
.prodetails{
    padding-bottom: 1rem;
    font-size: 1.1rem;
    text-align: justify;
}
.probtns{
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 0;
}
.probtns.btns{
    padding: 0.5rem 1rem;
}
@media screen and (max-width: 740px) {
    .projectcontainer{
        max-width:90%;
        margin: auto;
        grid-template-columns: 1fr;

    }
    
}